.header__wrapper{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 64px;
    color: white;
    margin-bottom: 20px;
    position: fixed;
    top: 0;
    background-color: #121213;
    width: 100%;
}

.header__logo{
    margin-left: 48px;
}

.header__search{
    display: flex;
    height: 40px;
    padding: 10px;
    align-items: center;
}

.header_searchContainer{
    height: 20px;
    border: 1px solid #31363A;
    border-radius: 4px;
    width: 375px;
}

.header_searchContainer > input {
    background-color: transparent;
    border: none;
    width: 100%;
    margin: 0 5px;
    color: white;
}

.header_searchContainer > input{
    outline: none;
}

.header__menuItems{
    margin: 0 48px;
}

.header__menuItems a {
    text-decoration: none;
    font-weight: bold;
    color: white;
    padding: 0px 10px;
}

.header__menuItems a:hover{
    color: #5AC53B;
}