.App {
  text-align: center;
}

*{
  margin: 0;
  font-family: Google Sans,arial,sans-serif-medium,sans-serif;
}

body{
  background: #121213;
  margin-top: 90px;
  
}

.app__body{
  width: 100%;
  height: 600px;
  display: flex;
  justify-content: center;  
}

.app__container{
  width: 80%;
  height: 100%;
  display: flex;
  padding: 0 48px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
